.listing {
  width: 100%;
  height: 100%;
  padding: 10rem calc((100vw - 1300px) / 2);

  &__container {
    height: 100%;
    width: 100%;
    padding: 2rem 1rem;
  }

  &__heading {
    font-size: 1.5rem;
    padding: 2rem 1rem;
    margin-bottom: 40px;

    @media screen and (max-width: 768px) {
      text-align: start;
    }
  }

  &__inforow {
    display: flex;
    flex-direction: row;
    padding: 1rem 0rem;

    @media screen and (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__infowrap {
    padding: 0rem 1rem;
    min-height: 550px;
    height: 100%;

    h2{
      margin-bottom: 1rem;
      font-weight: 400;

      @media screen and(max-width: 768px) {
        margin-bottom: 1rem;
      }
    }

    img {
      width: 100%;
      height: 100%;
      max-width: 600px;
      max-height: 400px;
      object-fit: cover;
      margin-bottom: 1rem;
    }

    &__imgtwo{
      width: 100%;
      height: 100%;
      max-width: 600px;
      max-height: 400px;
      object-fit: cover;
      margin-bottom: 1rem;
      margin-top: 120px;

      @media screen and (max-width: 768px) {
        margin-top: 0px;
      }
    }

    a {
      display: flex;
      align-items: center;
      text-decoration: none;
      color: #000d1a ;
      width: 140px;
      transition: 0.3s;

      &:hover {
        transform: translateY(-2px);
      }
    }
  }
}